import { Injectable } from '@angular/core';
import { ApplictionFactoryService } from '../appliction-factory.service'
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class PlansService extends ApplictionFactoryService{

  constructor(protected http: HttpClient) { 
    super(http)
  }

  //获取表格数据
  public get_GetCollegeDeadlines(){
    return new Promise<any>(resolve => {
      this.get('/applications/GetCollegeDeadlines').then(res => {
        resolve(res);
      });
    });
  }

  //表格展开数据
  public get_EditPlan(id){
    return new Promise<any>(resolve => {
      this.post('/applications/EditPlan?id='+id).then(res => {
        resolve(res);
      });
    });
  }

  //add Milestone
  public add_AddApplicationMilestone(param){
    return new Promise<any>(resolve => {
      this.post('/applications/AddApplicationMilestone',param).then(res => {
        resolve(res);
      });
    });
  }

  //delete Milestone
  public DeleteMilestones(id){
    return new Promise<any>(resolve => {
      this.get('/applications/DeleteMilestones?milestoneID='+id).then(res => {
        resolve(res);
      });
    });
  }

  //Mark as Completed
  public MarkAppPlanMilestonesCompleted(param){
    return new Promise<any>(resolve => {
      this.post('/applications/MarkAppPlanMilestonesCompleted',param).then(res => {
        resolve(res);
      });
    });
  }

  //Reset to Default
  public ResetAppPlan(id){
    return new Promise<any>(resolve => {
      this.get('/applications/ResetAppPlan?appPlanID='+id).then(res => {
        resolve(res);
      });
    });
  }

  //save as college Default
  public SaveAsCollegeDefault(id){
    return new Promise<any>(resolve => {
      this.get('/applications/SaveAsCollegeDefault?appPlanID='+id).then(res => {
        resolve(res);
      });
    });
  }

  //Apply to Non-approved Plans
  public UseAsTemplate(id){
    return new Promise<any>(resolve => {
      this.get(`/applications/UseAsTemplate?appPlanID=${id}`).then(res => {
        resolve(res);
      });
    });
  }

  //Approve Plan & Schedule Reminders
  public SetAppPlanAccepted(param){
    return new Promise<any>(resolve => {
      this.post(`/applications/SetAppPlanAccepted`,param).then(res => {
        resolve(res);
      });
    });
  }
}
