import { Injectable } from '@angular/core';
import { ApplictionFactoryService } from '../appliction-factory.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DashboardService extends ApplictionFactoryService {

  constructor(protected http: HttpClient) {
    super(http)
  }

  public getApplicationsDashboard(){
    return new Promise<any>(resolve => {
      this.get('/applications/GetApplicationsDashboard').then(res => {
        resolve(res);
      });
    });
  }
}
