import { Injectable } from '@angular/core';
import { ApplictionFactoryService } from '../appliction-factory.service'
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MyProgressService extends ApplictionFactoryService {

  constructor(protected http: HttpClient) {
    super(http)
  }

  //获取表格数据 1 = college, 2 = student deadline, 3 = milestone
  public api_get_MyProgres(id){
    return new Promise<any>(resolve => {
      this.get('/applications/GetProgress?id='+id).then(res => {
        resolve(res);
      });
    });
  }

  //delete
  public api_delete_MyProgres(id){
    return new Promise<any>(resolve => {
      this.get('/applications/DeleteMilestones?MilestoneID='+id).then(res => {
        resolve(res);
      });
    });
  }

  //update
  public api_updata_MyProgres(param){
    return new Promise<any>(resolve => {
      this.post('/applications/UpdateProgress',param).then(res => {
        resolve(res);
      });
    });
  }
  public api_updata_All_MyProgres(param){
    return new Promise<any>(resolve => {
      this.post('/applications/MarkAppPlanMilestonesCompleted',param).then(res => {
        resolve(res);
      });
    });
  }
}
