import { Component, OnInit } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { CalendarView,CalendarEvent} from 'angular-calendar';
import { DashboardService } from '../../service/service/appliction/dashboard.service'
import {MatSort, MatTableDataSource} from '@angular/material';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

const colors: any = {
  lateRegDate: {
    primary: '#f8948d',
    secondary: '#f8948d'
  },
  regDate: {
    primary: '#6fd59a',
    secondary: '#6fd59a'
  },
  today: {
    primary: '#f9b635',
    secondary: '#f9b635'
  },

};

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  plansChart:object;
  methodsChart:object;
  typesChart:object;
  upcomingKeyData:any;
  planProgressData:any;
  viewDate: Date = new Date();
  // testDate:Date=new Date('2019/1/12');
  // lateRegDate:Date =new Date('2019/1/20');
  // regDate:Date =new Date('2019/1/31');
  view: CalendarView = CalendarView.Month;

  AppsCompleted:Number;
  AppsNotCompleted:Number;
  AppsTotal:Number;
  Reminders:any={}
  chartFlag:boolean=false;
  constructor(private api:DashboardService) { }
  
  ngOnInit() {
    this.api.getApplicationsDashboard().then(res =>{
      this.AppsCompleted = res.AppsCompleted
      this.AppsNotCompleted = res.AppsNotCompleted
      this.AppsTotal = res.AppsTotal
      this.planProgressData = new MatTableDataSource<any>(res.AppPlanSummaries)
      this.upcomingKeyData = new MatTableDataSource<any>(res.UpcomingMilestones)
      this.Reminders.KeyMilestoneAdvanceNotice = res.KeyMilestoneAdvanceNotice
      this.Reminders.OtherMilestoneAdvanceNotice = res.OtherMilestoneAdvanceNotice
      this.Reminders.ReminderPreference = res.ReminderPreference
      this.initChart(
        {x:this.AppsCompleted,y:this.AppsNotCompleted},
        {x:this.getChartData(res.AppMethodDistribution)},
        {x:this.getChartData(res.AppTypeDistribution)}
      );
      let dateArr= []
      res.UpcomingMilestones.map(item =>{
        if(item.StudentDeadline != null){
          dateArr.push({
            title: item.CollegeName+item.MilestoneName+item.StudentDeadlineAsString+" student milestone",
            color: colors.regDate,
            start: new Date(item.StudentDeadlineAsCalendarString),
            allDay: true
          });
        }
        if(item.CollegeDeadline != null){
          dateArr.push({
            title: item.CollegeName+item.MilestoneName+item.StudentDeadlineAsString+" college milestone",
            color: colors.lateRegDate,
            start: new Date(item.CollegeDeadlineAsCalendarString),
            allDay: true
          });
        }
      })
      this.events = this.events.concat(dateArr);
    })
  }
  events: CalendarEvent[] = [
    {
      title: 'Today',
      color: colors.today,
      start: new Date(),
      allDay: true
    },
  ];

  initChart(plansChart,methodsChart,typeChart){
    this.chartFlag =true;
    this.plansChart = new Chart({
      chart: {
          type: 'bar',
          marginBottom:50
      },
      title:{
        text:null
      },
      xAxis: {
          categories: ['Approved', 'Not Approved'],
          title: {
              text: null
          },
          tickLength:0
      },
      yAxis: {
          min: 0,
          title: {
              text: 'Number of plans',
              align: 'high'
          },
          labels: {
              overflow: 'justify'
          },
          tickLength:0
      },
      tooltip: {
          valueSuffix: ''
      },
      legend:{
        enabled:false
      },
      plotOptions: {
          bar: {
            dataLabels: {
                enabled: true
                // ,allowOverlap 默认是 false，即不允许数据标签重叠
            },
            pointWidth:36
          }
      },
      credits: {
          enabled: false
      },
      series: [{
        data:[
          {
            color: '#60ae8b',
            y: plansChart.x
          },
          {
            color: '#c07d7b',
            y: plansChart.y
          }
        ]
      }]
    })
    this.methodsChart = new Chart({
      chart: {
          type: 'bar',
          marginBottom:50
      },
      title:{
        text:null
      },
      xAxis: {
          categories: methodsChart.x.x,
          title: {
              text: null
          },
          tickLength:0
      },
      yAxis: {
          min: 0,
          title: {
              text: 'Number of plans',
              align: 'high'
          },
          labels: {
              overflow: 'justify'
          },
          tickLength:0
      },
      colors:['#a8b7c7'],
      tooltip: {
          valueSuffix: ''
      },
      legend:{
        enabled:false
      },
      plotOptions: {
          bar: {
            dataLabels: {
                enabled: true
                // ,allowOverlap 默认是 false，即不允许数据标签重叠
            },
            pointWidth:30,
          },
          column: {
            colorByPoint:true
          }
      },
      credits: {
          enabled: false
      },
      series: [{
        data:methodsChart.x.y
      }]
    })
    this.typesChart = new Chart({
      chart: {
          type: 'bar',
          marginBottom:50
      },
      title:{
        text:null
      },
      xAxis: {
          categories: typeChart.x.x,
          title: {
              text: null
          },
          tickLength:0
      },
      yAxis: {
          min: 0,
          title: {
              text: 'Number of plans',
              align: 'high'
          },
          labels: {
              overflow: 'justify'
          },
          tickLength:0
      },
      colors:['#97c846' ],
      tooltip: {
          valueSuffix: ''
      },
      legend:{
        enabled:false
      },
      plotOptions: {
          bar: {
            dataLabels: {
                enabled: true
                // ,allowOverlap 默认是 false，即不允许数据标签重叠
            },
            pointWidth:36
          },
          column: {
            colorByPoint:true
          }
      },
      credits: {
          enabled: false
      },
      series: [{
        data:typeChart.x.y
      }]
    })
  }

  getChartData(data){
    if(data){
      let obj:any ={
        x:[],
        y:[]
      }
      Object.keys(data).forEach(function(key){
        obj.x.push(key)
        obj.y.push(data[key])
      });
      return obj
    }
  }

  upcomingKeyColumns: string[] = ['completed', 'student', 'college','milestone','college2'];
  planProgressColumns: string[] = ['college', 'appliction', 'appcompletion','applictionmethod'];
  exportPdf(){
    var element = document.getElementById("page");
    var width = element.clientWidth;//dom宽
    var height = element.clientHeight;//dom高
    var scale = 2;//放大倍数
    var canvas = document.createElement('canvas');
    canvas.width = width*scale;//canvas宽度
    canvas.height = height*scale;//canvas高度
        html2canvas(element, {
            dpi: window.devicePixelRatio*2,
            scale:scale,
            canvas:canvas,
            width:width,
            heigth:height,
        }).then(function(canvas) {
            var pdf = new jsPDF('p', 'mm', 'a4');    //A4纸，纵向
            var ctx = canvas.getContext('2d'),
                a4w = 190, a4h = 277,    //A4大小，210mm x 297mm，四边各保留10mm的边距，显示区域190x277
                imgHeight = Math.floor(a4h * canvas.width / a4w),    //按A4显示比例换算一页图像的像素高度
                renderedHeight = 0;

            while(renderedHeight < canvas.height) {
                var page = document.createElement("canvas");
                page.width = canvas.width;
                page.height = Math.min(imgHeight, canvas.height - renderedHeight);//可能内容不足一页

                //用getImageData剪裁指定区域，并画到前面创建的canvas对象中
                page.getContext('2d').putImageData(ctx.getImageData(0, renderedHeight, canvas.width, Math.min(imgHeight, canvas.height - renderedHeight)), 0, 0);
                pdf.addImage(page.toDataURL('image/jpeg', 1.0), 'JPEG', 10, 10, a4w, Math.min(a4h, a4w * page.height / page.width));    //添加图像到页面，保留10mm边距
                
                renderedHeight += imgHeight;
                if(renderedHeight < canvas.height)
                    pdf.addPage();//如果后面还有内容，添加一个空页
                //delete page;
            }
            pdf.save('content.pdf');
        });
  }
}
