import { Component, OnInit,Input ,Renderer2 ,Output, EventEmitter} from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import {format} from 'date-fns';

@Component({
  selector: 'app-milestone-table',
  templateUrl: './milestone-table.component.html',
  styleUrls: ['./milestone-table.component.css']
})
export class MilestoneTableComponent implements OnInit {

  @Input() dataSource;
  @Input() milestoneName;
  @Input() myPlanID;
  @Output() param = new EventEmitter();
  @Output() delete = new EventEmitter();
  @Output() makeAsCompleted = new EventEmitter();
  obj={
    appPlanID:'',
    admissionMilestoneID:'',
    statusID:'',
    studentDeadline:'',
    collegeDeadline:'',
    isKey:false,
    collegeDeadlineString:"",
    studentDeadlineString :""
  };
  milestoneColumns:string[] = ['select','AdmissionMilestoneName', 'AdmissionMilestoneCategoryName', 'CollegeDeadlineString','StudentDeadlineString','IsKey','StatusText','CompletionDate','ponit'];
  selection  = new SelectionModel<any>(true, []);
  
  constructor(private renderer:Renderer2) { }

  ngOnInit() {
  }

  addMilestone(table){
    this.obj.appPlanID = this.myPlanID;
    this.obj.collegeDeadline = format(this.obj.collegeDeadlineString, 'MM/DD/YYYY');
    this.obj.studentDeadline = format(this.obj.studentDeadlineString, 'MM/DD/YYYY');
    this.param.emit({param:this.obj, dataSource:this.dataSource,table:table});
  }

  deleteRow(table,element){
    this.delete.emit({id:element.ApplicationPlanMilestoneID, dataSource:this.dataSource,table:table});
  }

  markAsCompleted(){
    let arr= this.selection.selected.map(item =>{
      return item.ApplicationPlanMilestoneID;
    })
    this.makeAsCompleted.emit({milestoneIDs:{milestoneIDs:arr},dataSource:this.dataSource});
  }

  clearVal(){
    this.obj={
      appPlanID:'',
      admissionMilestoneID:'',
      statusID:'',
      studentDeadline:'',
      collegeDeadline:'',
      isKey:false,
      collegeDeadlineString:"",
      studentDeadlineString :""
    };
  }

  matTableClick(data,el){
    if(el.firstChild.style.display ==="block" || el.firstChild.style.display ===""){
      this.renderer.setStyle(el.lastChild,'display','block');
      this.renderer.setStyle(el.firstChild,'display','none');
    }else{
      this.renderer.setStyle(el.lastChild,'display','none');
      this.renderer.setStyle(el.firstChild,'display','block');
    }
    
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

}
