import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';

import { ApplicationComponent } from './application.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CollegeDeadlinesComponent } from './college-deadlines/college-deadlines.component';
import { MyApplicationComponent } from './my-application/my-application.component';
import { MyProgressComponent } from './my-progress/my-progress.component';
import { EditDialogComponent } from './edit-dialog/edit-dialog.component';
import { MilestoneTableComponent } from './milestone-table/milestone-table.component';

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: ApplicationComponent,
        children: [
          { path: '',component: DashboardComponent },
          { path: 'Dashboard',component: DashboardComponent },
          // { path: 'CollegeDeadlines',component: CollegeDeadlinesComponent },
          { path: 'MyApplication',component: CollegeDeadlinesComponent },
          { path: 'MyProgress',component: MyProgressComponent },
        ]
      }
    ]
    )
  ],
  declarations: [
    ApplicationComponent,
    DashboardComponent,
    CollegeDeadlinesComponent,
    MyApplicationComponent,
    MyProgressComponent,
    EditDialogComponent,
    MilestoneTableComponent
  ],
  entryComponents:[EditDialogComponent]
})
export class ApplicationModule { }
