import { Component, OnInit,ViewChild } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import { MatTableDataSource,MatSort } from '@angular/material';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {MatDatepickerInputEvent} from '@angular/material/datepicker';
import {format} from 'date-fns';
import {PlansService} from '../../service/service/appliction/plans.service';
import { LoadingService } from './../../shared/loading/service/loading.service';

@Component({
  selector: 'app-college-deadlines',
  templateUrl: './college-deadlines.component.html',
  styleUrls: ['./college-deadlines.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed, void', style({ height: '0px', minHeight: '0', visibility:'hidden' ,padding:'0'})),
      state('expanded', style({ height: '*',padding:'14px' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
      transition('expanded <=> void', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)'))
    ]),
  ],
})
export class CollegeDeadlinesComponent implements OnInit {
  selected:Number;
  chooseMethodFlag:string;
  @ViewChild('sortCol1') sortCol1: MatSort;
  @ViewChild('sortCol2') sortCol2: MatSort;

  constructor(private api:PlansService,public loading: LoadingService) { }

  ngOnInit() {
    this.loading.loading(true);
    this.selected =0;
    this.initDeadline();
    // this.initApplicationMethod();
  }
  tabsData = ['View by Deadline','View by Appliction Method'];

  tabsIndex(i){
    this.selected = i;
  }

  otherCallback(event:MatDatepickerInputEvent<Date>){
    return format(event.value, 'MM/DD/YYYY')
  }

  chooseApplictionMethod(index){
    this.chooseMethodFlag = index;
  }

  deadlineColumns: string[] = ['select', 'Name', 'REAString', 'ED1String','ED2String','EA1String','EA2String','PriorityString','RegularString','OtherString','drop'];
  milestoneColumns:string[] = ['select','milestone', 'type', 'CollegeDeadline','studentdeadline','key','status','date','ponit'];
  deadlineSelection = new SelectionModel<any>(true, []);
  deadlineListData:any=null;
  initDeadline(){
    this.api.get_GetCollegeDeadlines().then(res=>{
      this.loading.loading(false);
      let dropKey;
      dropKey = res.Data.map(item =>{
        item.border="";
        item.drop = false;
        item.deadline = [{REAString:item.REAString,ED1String:item.ED1String,ED2String:item.ED2String,EA1String:item.EA1String,EA2String:item.EA2String,
          PriorityString:item.PriorityString,RegularString:item.RegularString,OtherString:item.OtherString}]
        return item;
      })
      this.deadlineListData =new MatTableDataSource(dropKey);
      this.deadlineListData.sort = this.sortCol1;
      this.applicationMethodData = new MatTableDataSource(dropKey);
      this.applicationMethodData.sort = this.sortCol2;
    })
  }

  editPlans(el){
    if(el.drop && !el.milestone){
      this.api.get_EditPlan(el.MyPlanID).then(res=>{
        el.milestone =  new MatTableDataSource(res.Milestones);
        el.Majors = res.Majors;
        el.DeadlinesURL = res.DeadlinesURL;
        el.AdmissionMilestones = res.AdmissionMilestones;
        el.MajorsAsString = res.MajorsAsString;
      })
    }
  }

  addMilestone(param,milestone){
    this.api.add_AddApplicationMilestone(param.param).then(res=>{
      param.dataSource.data.push(res);
      param.table.renderRows();
      milestone.clearVal();
    })
  }

  deleteMilestoneEvet(param){
    this.api.DeleteMilestones(param.id).then(res =>{
      this.deleteTableRow(param.id,param.dataSource,param.table);
    })
  }

  makeAsCompletedEvet(param){
    this.api.MarkAppPlanMilestonesCompleted(param.milestoneIDs).then(res =>{
      for(let i=0;i<param.milestoneIDs.milestoneIDs.length;i++){
        for(let j =0;j<param.dataSource.data.length;j++){
          if(param.dataSource.data[j].ApplicationPlanMilestoneID == param.milestoneIDs.milestoneIDs[i]){
            param.dataSource.data[j].StatusText = res.statusText;
            param.dataSource.data[j].MilestoneStatus = res.statusID;
          }
        }
      }
    })
  }

  deleteTableRow(id,dataSource,table){
    const dsData = dataSource.data;
    const itemIndex = dsData.findIndex(obj => obj.ApplicationPlanMilestoneID == id);
    dataSource.data.splice(itemIndex, 1); 
    table.renderRows();
  }

  resetDefault(element){
    this.api.ResetAppPlan(element.MyPlanID).then(res =>{
      element.milestone =  new MatTableDataSource(res.Milestones);
    })
  }

  saveAsCollege(element){
    this.api.SaveAsCollegeDefault(element.MyPlanID).then(res =>{
      console.log(res);
    })
  }

  applyToNoPlans(element){
    this.api.UseAsTemplate(element.MyPlanID).then(res =>{
      console.log(res);
    })
  }

  approvePlan(){
    let arr = this.deadlineSelection.selected.map(item =>{
      return item.MyPlanID;
    })
    this.api.SetAppPlanAccepted({appPlanIDs:arr}).then(res =>{
      console.log(res);
    })
  }

  openStudy(select){
    select.open();
  }

  isAllSelected() {
    const numSelected = this.deadlineSelection.selected.length;
    const numRows = this.deadlineListData.data.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.deadlineSelection.clear() :
        this.deadlineListData.data.forEach(row => this.deadlineSelection.select(row));
  }

  applicationMethodColumns: string[] = ['select', 'Name', 'commonapp', 'coalition','other','drop'];
  chooseDeadlineColumns: string[] = ['rea', 'ed1','ed2','ea1','ea2','priority','regular','other'];
  applicationMethodSelection = new SelectionModel<any>(true, []);
  applicationMethodData:any=null;

}
