import { Component, OnInit,ViewChild } from '@angular/core';
import {MatSort, MatTableDataSource} from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { MatDialog } from '@angular/material';
import { EditDialogComponent } from '../edit-dialog/edit-dialog.component';
import { MyProgressService } from '../../service/service/appliction/my-progress.service';

@Component({
  selector: 'app-my-progress',
  templateUrl: './my-progress.component.html',
  styleUrls: ['./my-progress.component.css']
})
export class MyProgressComponent implements OnInit {

  constructor(public dialog: MatDialog,private api :MyProgressService) { }
  selected;
  selectList;
  dsData:any;
  activeTable;
  tabsData=['Sort by College','Sort by Deadline','Sort by Milestone'];
  @ViewChild(MatSort) sort: MatSort;
  ngOnInit() {
    this.selected =0;
    // this.init();
    //this.initSortByMilestone();
    this.initSortByCollege();
  }

  tabsIndex(i){
    this.selected =i;
    if(i ==0){
      this.activeTable = this.sortByCollegeData;
    }
    if(i ==1){
      this.activeTable =  this.sortByDeadlineData;
    }
    if(i ==2){
      this.activeTable = this.sortByMilestoneData;
    }
    if(i ==0 && !this.sortByCollegeData){
      this.initSortByCollege();
    }
    if(i ==1 && !this.sortByDeadlineData){
      this.init();
      this.activeTable =  this.sortByDeadlineData;
      // this.sortByDeadlineData[0].dataSource.sort = this.sort;
    }
    if(i ==2 && !this.sortByMilestoneData){
      this.initSortByMilestone();
    }
  }

  edit(data,ac,index){
    const dialogRef = this.dialog.open(EditDialogComponent, {
      height:'500px',
      width:'480px',
      data:{
        data:data,
        selectList:this.selectList
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      result.milestoneIDs = [result.milestoneIDs];
      this.api.api_updata_MyProgres(result).then(res =>{
        alert('success');
        this.updateDataTable(res.milestoneIDs,this.activeTable,ac,res,index);
      })
    });
  }

  setStutas(){
    let params=[];
    let dataS =this.sortByCollegeData;
    if((this.selected+1) ==1){
      dataS = this.sortByCollegeData;
    }else if((this.selected+1) ==2){
      dataS = this.sortByDeadlineData;
    }else{
      dataS = this.sortByMilestoneData;
    }
    for(let i =0;i<dataS.length;i++){
      for(let j=0;j<dataS[i].selection.selected.length;j++){
        params.push(dataS[i].selection.selected[j].ID)
      }
    }
    this.api.api_updata_All_MyProgres({milestoneIDs:params}).then(res =>{
      for(let i =0;i<dataS.length;i++){
        for(let j=0;j<dataS[i].selection.selected.length;j++){
          dataS[i].selection.selected[j].StatusText = res.statusText;
          dataS[i].selection.selected[j].MilestoneStatus = res.statusID;
        }
      }
    })

  }

  delete(data,ac,index){
    this.api.api_delete_MyProgres(data.ID).then(res=>{
      if(!res.errorMessage){
        alert('success')
        this.deleteRowDataTable(data.ID,this.activeTable,ac,index);
      }
    })
    //this.deleteRowDataTable(data.ID,this.activeTable,ac);
  }

  deleteRowDataTable(id,dataSource,ac,index){
    this.dsData = dataSource[index].dataSource.data;
    const itemIndex = this.dsData.findIndex(obj => {return obj.ID == id});
    dataSource[index].dataSource.data.splice(itemIndex, 1); 
    ac.renderRows();
  }

  updateDataTable(id,dataSource,ac,formValue,index=0){
    const dsData = dataSource[index].dataSource.data;
    const itemIndex = dsData.findIndex(obj => obj.ID == id[0]);
    const newVal = dataSource[index].dataSource.data[itemIndex];
    newVal.StatusNote = formValue.statusNote;
    newVal.StatusText = formValue.statusText;
    newVal.StudentDeadline=  formValue.studentDeadline;
    newVal.StudentDeadlineString = formValue.studentDeadlineString;
    dataSource[index].dataSource.data.splice(itemIndex,1,newVal);
    ac.renderRows();
  }

  sortByDeadlineData:any=null;
  sortByDeadlineColumns: string[] = ['select', 'StudentDeadlineString', 'SollegeName', 'type','ApplicationMilestoneName','Description','CollegeDeadlineString','StatusText','StatusNote','point'];
  init(){
    let queryId = 2;
    let arr =[];
    this.api.api_get_MyProgres(queryId).then(res =>{
      arr.push({dataSource:new MatTableDataSource<any>(res.PlansProgress),selection:new SelectionModel<any>(true, [])});
      this.sortByDeadlineData = arr;
      this.selectList = res.StatusCategories;
      this.sortByDeadlineData[0].dataSource.sort = this.sort;
      this.activeTable =  this.sortByDeadlineData;
    })
  }

  sortByMilestoneData:any=null;
  sortByMilestoneColumns: string[] = ['select', 'college', 'type','description','collegedeadline','studentdeadline','status','stutesnote','point'];
  initSortByMilestone(){
    let queryId = 3;
    let arr =[];
    let index=0;
    this.api.api_get_MyProgres(queryId).then(res =>{
      for(let key in res.PlansProgressDic){
        arr.push({dataSource:new MatTableDataSource<any>(res.PlansProgressDic[key]),selection:new SelectionModel<any>(true, []),title:key,num:index++})
      }
      this.sortByMilestoneData = arr;
      this.activeTable = this.sortByMilestoneData;
    })
  }

  sortByCollegeData:any=null;
  sortByCollegeColumns: string[] = ['select', 'milestone','type','description','collegedeadline','studentdeadline','status','stutesnote','point'];
  initSortByCollege(){
    let queryId = 1;
    let arr = [];
    let index=0;
    this.api.api_get_MyProgres(queryId).then(res =>{
      for(let key in res.PlansProgressDic){
        arr.push({dataSource:new MatTableDataSource<any>(res.PlansProgressDic[key]),selection:new SelectionModel<any>(true, []),title:key,num:index++})
      }
      this.sortByCollegeData = arr;
      this.selectList = res.StatusCategories;
      this.activeTable =this.sortByCollegeData;
    })
    
  }

  isAllSelected(item) {
    const numSelected = item.selection.selected.length;
    const numRows = item.dataSource.data.length;
    return numSelected === numRows;
  }

  masterToggle(item) {
    this.isAllSelected(item) ?
        item.selection.clear() :
        item.dataSource.data.forEach(row =>{item.selection.select(row)});
  }

}
